import axios from "axios";
import qs from 'qs';
import omui from '../../src/index'
const { Toast, get, add, remove } = omui
import ev from '../event'

let BASEURL;
// ||'test'||'127'||'192'||'172'
const testorigin=()=>{
    let testUrl = [
        'localhost','test','127','192','172'
    ];
    for(let i =0;i<testUrl.length;i++){
        if(location.origin.indexOf(testUrl[i])>-1){
            return true
        }
    }
}

if(testorigin()){
    console.log('DEV')
    // BASEURL = 'http://test.api.fufentong.com/master/'
    BASEURL = 'http://lithii.top:2334/'
}else(
    BASEURL = 'http://lithii.top:2334/'

)
const service  = axios.create({
    baseURL:BASEURL,
    timeout:0
});
const delKey = (data) =>{
    Object.keys(data).forEach(key=>{
        if(data[key]===null||data[key]===''){
            delete data[key]
        }
    })
    return data 
};
service.interceptors.request.use(
conf=>{
    conf.method === 'get'?
    conf.params = delKey(Object.assign({},conf.params)):
    conf.data = qs.stringify(delKey(Object.assign({},conf.data)));
    // conf.headers['Content-Type'] = 'application/x-www-form-urlencoded';
    return conf;
},err=>{
    Toast(err);
    Promise.reject(err)
})

service.interceptors.response.use(
    res=>{
        if(res.data.code===200){
            return res.data
        }
        else{
            Toast(res.data.msg);
            return res.data
        }
    },
    err=>{
        if(err){
            let text = '';
            Toast(text);
        }
        return Promise.reject(err)
    }
)
export default service;


