import service from './utils/http'


export const getToday = () => {
    return service({
        url: 'today',
        method: 'get'
    })
}

export const getOne = id => {
    return service({
        url: `one?id=${id}`,
        method: 'get'
    })
}
export const getArticle = id => {
    return service({
        url: `article?id=${id}`,
        method: 'get'
    })
}
export const getQuestion = id => {
    return service({
        url: `question?id=${id}`,
        method: 'get'
    })
}


